* {
  font-family: 'Roboto', sans-serif;
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html {
  overflow-x: hidden;
}

body {
  padding-top: 98px;
}

.noscroll {
  overflow-y: hidden;
}

h1 {
  font-size: 24px;
}

p {
  line-height: 150%;
}

b {
  font-weight: 700;
}

.card {
  border-radius: 13.5px;
  background: white;
  display: flex;
  flex-flow: column nowrap;
  box-shadow: 0px 6px 8px rgba(98, 98, 98, 0.18), 0px 2px 2px rgba(98, 98, 98, 0.18), 0px 0px 2px rgba(98, 98, 98, 0.18);
}

.card-child {
  padding:32px
}

.card-title {
  font-size: 24px;
  font-weight: 600;
  margin-bottom: 20px;
}

.card-description {
  color: #565656;
  font-weight: 300;
  font-size: 18px;
  line-height: 150%;
  margin-bottom: 14px;
  font-weight: 500;
}

.card-image {
  border-radius: 13.5px 13.5px 0 0;
  width: 100%;
}

.field {
  margin: 40px 0;
  display: flex;
  align-items: center;
  flex-flow: row nowrap;
  justify-content: space-between;
}

.field-content {
  display: flex;  
  flex-flow: row nowrap;
  justify-content: flex-start;
  align-items: flex-start;
  width: 100%;
}

.field-content div:first-child {
  margin-right:16px;
}

.title {
  font-size: 36px;
  color: #AE0B05;
}

.sub-title {
  font-size: 24px;
  color: #000;
}

.sub-description {
  color: #3D3D3D;
  margin-top:10px;
}

.indications {
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  align-items: space-around;
}

.indications div {
  margin-right: 40px;
}

.indications div span {
  letter-spacing: 0.0125em;
  display: block;
  margin: 15px 0;
  font-size: 18px;
  font-weight: 600;
}

.word-break {
  height:10px;
  display: block;
}

.result-input {
  display: inline-block;
  max-width:38px;
  max-height:38px;
  min-height: 38px;
  text-align: center;
  font-size: 16px;
  -webkit-appearance: none;
  outline: none;
  border: 1px solid #A3A3A3;
  border-radius: 9.5px;
  margin-right: 15px;
}

.result-form {
  margin: 30px 0;
}

.result-input {
  margin-top: 20px;
}

.input-checkbox {
  width: 20px;
  height: 20px;
}

.input-text {
  display: inline-block;
  padding: 12px 20px;
  border: 1px solid #A3A3A3;
  border-radius: 9.5px;
  font-size: 14px;
  width: 100%;
  margin: 15px 0 30px;
  font-weight: 500;
}

.input-text::placeholder {
  font-style: italic;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance:textfield;
}

.field-title {
  font-size: 24px;
  margin-bottom: 8px;
}

.field-number {
  font-size: 24px;
}

.field-description {
  font-size: 18px;
  font-style: italic;
}

.result-scale > span {
  display: block;
  font-weight: 600;
  font-size: 16px;
  margin-top: 20px;
}

.checkboxes::after {
  content: "";
  clear: both;
  display: table;
  margin: 30px 0;
}

.input-checkbox {
  display: block;
  margin: 10px 0;
}

.result-content {
  width: 100%;
  position: relative;
}

.scale-info {
  display: block;
  height: auto;
  font-size: 11.5px;
}

.scale-info > span {
  margin-right: 7px;
}

.button {
  font-size: 18px;
  display: inline-block;
  margin: 0 24px 10px 0;
  border: 2px solid #AE0B05;
  padding: 10px 15px;
  text-decoration: none;
  color: black;
  font-weight: 300;
  border-radius: 13.5px;
}

.button:first-child {
  margin-left: 0;
}

.button:last-child {
  margin-right: 0;
}

.sent {
  color: green;
}

.error {
  color: #AE0B05
}

.none {
  color: green;
}

.mild {
  color: green;
}

.moderate {
  color: #ECA93B;
}

.accute {
  color: #D12E26;
}

.inactive {
  color: #C3C3C3;
}

.banners {
  position: fixed;
  left: 0;
  display: flex;
  flex-flow: column nowrap;
  height: 100%;
  width: 20px;
} 

[class^="banner-"] {
  height: 100%;
}

hr {
  border: 1px solid black;
  display: block;
  margin-top: 10px;
}

.active {
  background-color: #AE0B05;
  color: white;
}

.small-span {
  display: inline-block;
  margin: 15px 0;
  font-size: 14px;
}

.banner-1 {
  background-color: #ECA93B;
}
.banner-2 {
  background-color: #BB2753;
}
.banner-3 {
  background-color: #DD6E2D;
}
.banner-4 {
  background-color: #A0251A;
}
.banner-5 {
  background-color: #D12E26;
}

.landing {
  text-align:center;
  max-width: 300px;
  margin: 50px auto;
}

.landing > a,
.landing > img {
  display: block;
}

.landing > p {
  text-align: left;
}

.link {
  position: relative;
  display: inline-block;
  padding: 12px 20px;
  background-color: #AE0B05;
  color: white;
  margin: 20px 0 40px;
  text-decoration: none;
  font-size: 18px;
  box-shadow: 0px 1px 2px rgba(98, 98, 98, 0.18), 0px 0px 2px rgba(98, 98, 98, 0.18);
  border-radius: 13.5px;
  width:100%;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
}

button {
  outline: none;
  border: 0;
  cursor: pointer;
}

.previous {
  white-space: nowrap;
  margin-right: 15px;
  text-decoration: none;
  color: #000;
  background-color: white;
  box-shadow: 0px 1px 2px rgba(98, 98, 98, 0.18), 0px 0px 2px rgba(98, 98, 98, 0.18);
}

.logo-footer {
  display: inline-block;
  min-width: 200px;
  max-width:200px;
}


.watermark {
  display: block;
  max-width:200px;
  margin: 0 auto 30px;
}

.arrow-back {
  background: url('./img/arrow-back.svg') no-repeat center;
  width: 25px;
  height: 25px;
  margin-right: 10px;
}

.close-link {
  position: absolute;
  right:8px;
  top:8px;
  width: 44px;
  height: 44px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.close-icon {
  background: url('./img/close.svg') no-repeat center;
  width:32px;
  height:32px;
}
.arrow-back,
.close-icon {
  background-size: contain;
  display: inline-block;
}

footer {
  padding: 20px 40px;
  max-width: 800px;
  margin: 0 auto;
  background-color: #ededed;
}

.footer-flex {
  display: flex;
  flex-flow: column wrap;
  justify-content: flex-start;
  align-items: flex-start;
}

.footer-child {
  padding: 10px;
  margin: 10px 0;
  max-width: 800;
}

.container {
  max-width: 450px;
  margin: 0 auto;
}

.result {
  padding: 1px;
  max-width: 800px;
  margin: 0 auto;
}

.nav {
  padding: 0 20px;
  box-shadow: 0px 2px 0px rgba(0, 0, 0, 0.12), 0px 1px 0px rgba(0, 0, 0, 0.12);
  background-color: white;
  position: fixed;
  top:0;
  left:0;
  z-index: 2;
  width:100%;
}

.popup {
