  margin: 50px auto;
  background-color: #fff;
  border-radius: 12.5px;
  box-shadow: 0px 26px 32px rgba(98, 98, 98, 0.12), 0px 10px 16px rgba(98, 98, 98, 0.12), 0px 6px 8px rgba(98, 98, 98, 0.12), 0px 3px 2px rgba(98, 98, 98, 0.12), 0px 0px 2px rgba(98, 98, 98, 0.18);
  position: relative;
  z-index: 100;
}

.popup-content {
  padding: 32px;
}

.popup-top-bar {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 14px;
}

.popup-top-bar .sub-title {
  padding-right: 48px;
}

.text-gray {
  color: #707070;
}

.text-medium {
  color: #000;
  font-weight: 500;
}

.input-popup {
  margin: 10px 0;
}

.popup-section {
  border-top: 1px dashed #707070;
  padding: 32px;
  text-align: center;
}

.popup-section:last-child {
  padding-bottom: 0;
}

.popup-link {
  margin-bottom: 0;
  margin-top: 14px;
}

.copyright {
  font-size: 18px;
  color: #707070;
  margin-bottom: 8px;
}

.cta-button {
  display: inline-block;
  max-width:160px;
  color: #000;
  background: #F5F5F5;
  box-shadow: 0px 10px 16px rgba(98, 98, 98, 0.12), 0px 6px 4px rgba(98, 98, 98, 0.12), 0px 3px 2px rgba(98, 98, 98, 0.12), 0px 0px 2px rgba(98, 98, 98, 0.18);
}

.nav-container {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: flex-end;
  max-width: 1400px;
  margin: 0 auto;
  height: 98px;
  padding-bottom:10px;
}

.bottom-buttons {
  display: flex;
  flex-flow: row wrap;
  justify-content: stretch;
  align-items: center;
}

.bottom-buttons div {
  margin-right: 15px;
}

.bottom-buttons div:first-child {
  flex-grow: 0;
}

.bottom-buttons div:last-child {
  margin: 0;
  flex-grow:1;
}

.settings-icon {
  display: inline-block;
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  width:32px;
  height:32px;
}

.info-icon {
  background-image: url('./img/info.svg');
}

.settings-icon {
  background-image: url('./img/settings.svg');
}

.settings-box,
.info-box {
  max-width: 95%;
  margin: 0 auto;
  background-color: #ededed;
  padding: 15px;
  margin-bottom: 20px;
}

.info-link {
  display: inline-block;
  width:33px;
  height: 33px;
}

.settings-link {
  display: inline-block;
  width: 44px;
  height: 44px;
  opacity: 1;
  align-self: flex-start;
  margin-top: 6px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.info-icon:hover {
  background-image: url('./img/info-filled.svg');
}

.info-filled {
  background-image: url('./img/info-filled.svg');
}

.settings-content {
  margin:20px 0;
  display: flex;
  flex-flow: row nowrap;
}

.section {
  position: relative;
  background-color:#ededed;
  padding: 25px 0;
  width: 100vw;
  margin-left: calc(50% - 50vw);
}

.inner-content {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px 60px;
}

.settings-content > div {
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-around;
}

.settings-content > div > label,
.settings-content > div > input {
  display: inline-block;
  /* margin: 5px 0; */
}

.settings-content > div:last-child {
  margin-left: 20px;
}

.spinner {
  /* margin: 100px auto 0; */
  width: 70px;
  text-align: center;
}

.spinner > div {
  width: 12px;
  height: 12px;
  background-color: #D12E26;

  border-radius: 100%;
  display: inline-block;
  -webkit-animation: sk-bouncedelay 1.4s infinite ease-in-out both;
  animation: sk-bouncedelay 1.4s infinite ease-in-out both;
}

.spinner .bounce1 {
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}

.spinner .bounce2 {
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}

@-webkit-keyframes sk-bouncedelay {
  0%, 80%, 100% { -webkit-transform: scale(0) }
  40% { -webkit-transform: scale(1.0) }
}

@keyframes sk-bouncedelay {
  0%, 80%, 100% { 
    -webkit-transform: scale(0);
    transform: scale(0);
  } 40% { 
    -webkit-transform: scale(1.0);
    transform: scale(1.0);
  }
}

.checkbox-labels {
  display: flex;
  flex-flow: column;
  justify-content: space-between;
}

.checkbox-container {
  display: block;
  position: relative;
  height:30px;
  width:30px;
  margin-bottom: 15px;
  cursor: pointer;
  font-size: 22px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.checkbox-container:last-child {
  margin: 0;
}

/* Hide the browser's default checkbox */
.checkbox-container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

/* Create a custom checkbox */
.checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: #eee;
}

/* On mouse-over, add a grey background color */
.checkbox-container:hover input ~ .checkmark {
  background-color: #ccc;
}

/* When the checkbox is checked, add a blue background */
.checkbox-container input:checked ~ .checkmark {
  background-color: #2196F3;
}

/* Create the checkmark/indicator (hidden when not checked) */
.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the checkmark when checked */
.checkbox-container input:checked ~ .checkmark:after {
  display: block;
}

/* Style the checkmark/indicator */
.checkbox-container .checkmark:after {
  left: 11px;
  top: 7px;
  width: 5px;
  height: 10px;
  border: solid white;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}

.checkbox-button {
  cursor: pointer;
  margin-right: 20px;
  background-color:#EFEFEF;
  border-radius:4px;
  border:1px solid #D0D0D0;
  overflow:auto;
  float:left;
  outline: none;
  border: 0px;
}


.checkbox-button label {
  cursor: pointer;
  float:right;
}

.checkbox-button label span {
  text-align:center;
  padding:3px 0px;
  display:block;
  color: #000;
  border: 2px solid #707070;
  border-radius:13.5px;
  padding: 10px 16px;
}

.checkbox-button label input {
  display: none;
}

.checkbox-button input:checked + span {
  background-color:#AE0B05;
  border-color: #AE0B05;
  color:#fff;
}

.checkbox-button input:checked:hover + span {
  background-color:#AE0B05;
  border-color: #AE0B05;
  color:#fff;
}

.hidden {
  display: none;
}

.disabled {
  cursor: default;
  pointer-events: none;
}

.disabled-link {
  opacity: .5;
  background-color:gray;
  cursor: default;
}

/* Tooltip text */
.tooltiptext {
  visibility: hidden;
  width: 300px;
  white-space: pre-wrap;
  background-color: black;
  color: #fff;
  text-align: center;
  padding: 5px 0;
  border-radius: 6px;
  font-size: 16px;
 
  /* Position the tooltip text - see examples below! */
  position: absolute;
  bottom: 120%;
  left: 25%;
  margin-left: -60px;
  z-index: 1;
}

.tooltiptext::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color:#000 transparent transparent transparent;
}

/* Show the tooltip text when you mouse over the tooltip container */
.link:hover .tooltiptext {
  visibility: visible;
}

.disabled-field,
.disabled-field .button {
  color: #707070;
}

.disabled-field .button {
  border-color: #707070;
  cursor: default;
  pointer-events: none;
}

@media screen and (max-width: 500px) {
  .title {
    font-size: 34px;
  }

  .nav {
    padding: 0 16px;
  }

  .inner-content {
    padding: 0 16px;
  }

  .popup {
    padding: 0 16px;
  }

  .link {
    padding: 12px 10px;
    font-size: 16px;
  }

  .previous {
    white-space: normal;
  }

  .field-title,
  .field-number {
    font-size: 20px;
  }

  .field-description,
  .button {
    font-size: 16px;
  }

  .button {
    font-size: 15px;
    margin: 0 10px 10px 0;
    padding: 10px 12px;
  }

  .field-content div:first-child {
    margin-right:6px;
  }
  
}